import React, { useState } from "react"
import { graphql, Link } from "gatsby"
import * as styles from "../styles/tags.module.less"
import useStore from "../store/index"
import kebabCase from "lodash/kebabCase"
// antd
import { Drawer, Breadcrumb } from "antd"
import "antd/dist/antd.css"
// Components
import MainLayout from "../components/mainLayout"
import Seo from "../components/seo"
import BlogItem from "../components/blogItem"
import SideMenu from "../components/sideMenu"
const Tags = ({ pageContext, data, location }) => {
  // tagtag
  const { tag } = pageContext

  // store
  const { knowledgeType, Data } = useStore.getState()
  // store
  const { setState } = useStore
  setState({ tag, lastPathName: location.pathname })

  // 
  let tagData
  if (knowledgeType === "") {
    tagData = Data.trainee
  } else if (knowledgeType === "") {
    tagData = Data.knowledge
  } else {
    tagData = []
  }

  // tag
  const { nodes } = data.allMarkdownRemark

  // 
  const [menuToggle, setMenuToggle] = useState(false)

  // antddraw
  const [visible, setVisible] = useState(false)

  // 
  const showDrawer = () => {
    setMenuToggle(!menuToggle)
    setVisible(!visible)
  }
  // 
  const onClose = () => {
    setVisible(false)
  }
  // key,
  let key, firstTag
  tagData.forEach(item => {
    if (item.stepTags.includes(tag)) {
      key = item.step
      firstTag = item.stepTags[0]
      setState({ key, firstTag })
    }
  })

  return (
    <div>
      <Seo title={`${tag}`} />
      <MainLayout path={location.pathname}>
        <div className={styles.wrapBread}>
          <div className={styles.pullDown}>
            <Breadcrumb>
              <Breadcrumb.Item>
                <Link to="/frontEnd"></Link>
              </Breadcrumb.Item>
              {knowledgeType === "" ? (
                <Breadcrumb.Item>
                  <Link to="/tags/react"></Link>
                </Breadcrumb.Item>
              ) : null}
              {tagData.length ? (
                <Breadcrumb.Item>
                  <Link to={`/tags/${kebabCase(firstTag)}`}>{key}</Link>
                </Breadcrumb.Item>
              ) : null}

              <Breadcrumb.Item>{tag}</Breadcrumb.Item>
            </Breadcrumb>
            <div
              className={styles.arrowBox}
              aria-hidden="true"
              onClick={showDrawer}
            >
              <span className={styles.changeWord}> </span>
              <span className={menuToggle ? styles.bottom : styles.bottomAfter}>
                <i className={styles.bottomArrow1}></i>
                <i className={styles.bottomArrow2}></i>
              </span>
            </div>
          </div>
        </div>
        <div className={styles.BottomContent}>
          <div className={styles.wrapContent}>
            <div className={styles.wrapSide}>
              <SideMenu pathname={location.pathname} tag={tag} />
            </div>
            <div className={styles.detail}>
              <div className={styles.detailContent}>
                <Drawer
                  placement="top"
                  closable={false}
                  onClose={onClose}
                  visible={visible}
                  getContainer={false}
                  style={{ position: "absolute" }}
                  height="350px"
                >
                  <SideMenu pathname={location.pathname} tag={tag} />
                </Drawer>
                <BlogItem nodes={nodes} tag={tag} />
              </div>
            </div>
          </div>
        </div>
      </MainLayout>
    </div>
  )
}

export default Tags

export const pageQuery = graphql`
  query ($tag: String) {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      group(field: frontmatter___tags) {
        fieldValue
        totalCount
      }
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "YYYY-MM-DD")
          title
          description
          author
          order
          tags
          type
        }
      }

      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
          }
        }
      }
    }
  }
`
